// antd 需引入的函示庫組
import { Card, Form, Input, Checkbox, Button, message, Divider } from 'antd'
// import 'antd/dist/reset.css'
import logo from '@/assets/logo.png'
import { Link, useNavigate } from 'react-router-dom'
// 導入樣式文件
import './index.scss'
import { useStore } from '@/store'
import axios from 'axios'
import { v4 as uuid } from 'uuid'
import { saveUser, getUser } from '@/utils/storageUtils'
import { saveRem } from '@/utils/remstoreUtils'
import memoryUser from '../../utils/memoryUtils'
import { useState } from 'react'
//import memoryUtils from '@/share/storage'
import {
  CopyrightOutlined,
} from '@ant-design/icons'

function Login () {
  const { loginStore } = useStore()
  const user = getUser('user_key')
  const user_tel = Object.keys(user).length !== 0 ? user : null
  const navigate = useNavigate()
  const onFinish = async values => {
    console.log(values)
    // values: 放置的是所有表單項中用戶輸入的內容
    // todo: 登錄
    const id = uuid()
    const { tel, remember } = values
    const rem = remember ? "1" : "0"
    console.log(remember)
    try {
      await loginStore.login({ id, tel })
      memoryUser.tel = tel
      saveUser(tel)
      saveRem(rem)
      // 跳轉首頁
      navigate('/Register')
      // 提示用戶
      message.success('登入成功')
    } catch (e) {
      message.error(e.response?.data?.message || '登入失敗')
    }
  }

  const onFinishFailed = (errorInfo) => {
    console.log('Failed', errorInfo)
    // 提示用戶
    message.error('登入失敗')
  }

  const onChange = (e) => {
    console.log(`checked = ${e.target.checked}`)
  }

  return (
    <div>
      <div className='login'>
        <Card className='login-container'>
          <img className='login-logo' src={logo} alt='' />
          {/* 登入表單 */}
          {/* 子項用到的觸發事件，需要在 Form 中都聲明一下才可以 */}
          <Form
            validateTrigger={['onBlur', 'onChange']}
            initialValues={{
              tel: user_tel,
              remember: true
            }}
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
          >
            <Form.Item
              name="tel"
              rules={[
                {
                  required: true,
                  message: '手機號碼不能為空!',
                },
                {
                  pattern: /^09|^04\d{8}$/,
                  message: '手機號碼格式錯誤',
                  validateTrigger: 'onBlur'
                }
              ]}
            >
              <Input size="large" placeholder="請输入手機號碼" />
            </Form.Item>
            <Form.Item>
              <Form.Item
                name="remember"
                valuePropName="checked"
                noStyle
              >
                <Checkbox className="login-checkbox-label" onChange={onChange} >
                  記住我
                </Checkbox>
              </Form.Item>
            </Form.Item>

            <Form.Item style={{ textAlign: 'center' }}>
              <Form.Item>
                <Button type="primary" htmlType="submit" size="large" block>
                  登入
                </Button>
              </Form.Item>
            </Form.Item>
          </Form>
        </Card>

        <div className='setInfo'>
          <Divider className='dash' style={{ width: '100%', color: '#4cb804', fontWeight: 'bold', fontSize: 18, borderColor: 'white' }}><CopyrightOutlined /> {new Date().getFullYear()}-遠漢科技</Divider>
          <p className='content'>本應用為輔助檢測用途，數據僅供參考</p>
        </div>
      </div>
    </div>
  )
}

export default Login